.faq-container {
  background-color: rgb(22, 22, 22);
  padding-bottom: 50px;
}

.faq-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  color: white;
  font-size: 24px;
}

.accordion {
  padding: 10px;
  margin-top: 10px;
  margin-bottom: 10px;
}

.accordion__question p {
  margin: 5px;
  padding: 0;
  font-family: Verdana, sans-serif;
  font-size: 20px;
  color: white;
}

.accordion__answer p {
  margin: 5px;
  padding: 10px;
  font-size: 14px;
  font-family: Verdana, sans-serif;
  color: white;
  line-height: 2;
}

.accordion:hover {
  cursor: pointer;
}

.accordion__answer {
  display: none;
}

.accordion.active .accordion__answer {
  display: block;
}

.faq-answer-header {
  margin: 10px 0 35px -5px;
  display: block;
}

.first-stage,
.second-stage,
.third-stage,
.fourth-stage {
  margin: 0 0 5px 10px;
  display: block;
}

.faq-important-message {
  display: block;
  margin: 35px 0 0 -15px;
}

svg {
  color: white;
}

.faq-down-arrow {
  color: rgb(244, 244, 244);
  margin-left: 100%;
  margin-top: -30px;
  width: 24px;
  height: 24px;
}

hr {
  margin-top: 20px;
  border-color: rgb(63, 64, 64);
}

.faq-header span {
  margin-left: 45%;
  font-size: 20px;
  cursor: pointer;
  color: #0bc1f9;
}
.faq-header span:hover {
  color: white;
  transition: all 0.5s ease;
}

.hidden {
  display: none;
}
